<template>
<div class="CalculateDegree padding students cur-poin">
    <!-- 能力达成度分析 -->
    <div class="breadcrumb-pd" v-show="showanalysis">
        <el-breadcrumb separator=">">
            <el-breadcrumb-item :to="{ path: '/Calculate/CalculateDegree' }">达成度计算</el-breadcrumb-item>
            <el-breadcrumb-item>
                毕业要求
            </el-breadcrumb-item>
        </el-breadcrumb>
    </div>
    <div v-show="showanalysis" class="analysis">
        <div class="analysis_top">
            <div class="analysis_left" @click="goback" v-if="title">
              <svgIcon icon-class="iconfanhui" size="14"></svgIcon>
                <span>返回上级</span>
            </div>
            <p v-if="title">{{title}}毕业要求达成度分析</p>
            <div class="analysis_right" v-if="title">
              <svgIcon icon-class="icondaochu-excel" size="14"></svgIcon>
                <!-- <img src="@/assets/img/daochu-excel.png" alt=""> -->
                <span>导出Excel</span>
            </div>
        </div>
        <div class="analysis_botm">
            <img src="@/assets/img/dachengdu02.png" alt="" :style="{'width': fullWidth == 1920 ? '1131px' : '83%'}">
        </div>
    </div>
    <div class="center_breadcrumb" v-show="showchoosing">
        <Crumb />
    </div>
    <div v-show="showchoosing" class="analysis_Computer">
            <img src="../../assets/img/bg-dachengdu.png" alt="">
            <p>利用学生模型能力算法，生成多维度分析报告，利用评价、反馈和持续改进</p>
            <img src="@/assets/img/dachengdu01.png" alt :style="{'width': fullWidth == 1920 ? '522px' : '450px'}">
        </div>
    <div style="background-color: #fff;padding-bottom: 40px;" v-show="showchoosing">
        
        <div class="dv" v-show="showchoosing">
            <el-col :span="8">
                <div style="float: left; margin-left: 120px;width: 75%;">
                    <span>年级</span>
                    <el-select v-model="value" placeholder="请选择年级">
                        <el-option v-for="item in cities" :key="item" :label="item" :value="item">
                        </el-option>
                    </el-select>
                </div>
            </el-col>
            <el-col :span="8">
                <div style="float: left; margin-left: 70px;width: 75%;">
                    <span>班级</span>
                    <el-select v-model="value1" placeholder="请选择班级">
                        <el-option v-for="item in cities1" :key="item.id" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </div>
            </el-col>
            <el-col :span="8">
                <div style="float: left;margin-left: 20px;width: 75%;">
                    <span>学生</span>
                    <el-select v-model="value2" placeholder="可模糊搜索学号/姓名" filterable>
                        <el-option v-for="item in cities2" :key="item.id" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </div>
            </el-col>
            
        </div>
        <div class="classManagement-btn" v-show="showchoosing" @click="calculate">
            <!-- <el-button type="primary" @click="calculate">开始分析</el-button> -->
            <span  >开始分析</span>
        </div>
    </div>
    <div class="bgc-fff" v-show="falgeshow">
        <!-- <div class="DegreeAchievement-dv7">
            <span>{{title}}</span>
            <span>导出结果</span>
        </div> -->
        <div class="echarts"  v-show="showanalysis" style="background: #fff">
            <!-- 雷达图引用 -->
            <echarts1 :chartsData="chartsData"></echarts1>
        </div>
        <div  class="chartsData_left"  v-show="showanalysis">
            <ul>
                <li v-for="(item,index) in tableData" :key="index" @click="handdle(item)">
                    <p class="chartsData_p">
                        <span class="chartsData_sp"></span>
                        <i>{{index+1}}</i>
                    </p>
                    <div class="chartsData_leftdv">
                        <p class="chartsData_frist">毕业要求{{index+1}}：{{item.claimcontent}}</p>
                        <p class="chartsData_tow">支撑目标：{{item.goals}}</p>
                        <div class="chartsData_bottom">
                            <span >
                                直接达成值：{{item.analysis}}
                            </span>
                            <span >
                                间接达成值：{{item.score}}
                            </span>
                            <span >
                                期望达成值：{{item.target}}
                            </span>
                        </div>
                    </div>
                    
                </li>
            </ul>
        </div>
    </div>

</div>
</template>

<script>
import echarts1 from "@/components/echaars/echars1"
export default {
    components: {
        echarts1,
    },
    data() {
        return {
            cities: [],
            value: "",
            cities1: [],
            value1: "",
            cities2: [],
            value2: "",
            tableData: [],
            chartsData: {},
            title: '',
            falgeshow: false,
            showanalysis:false,
            showchoosing:true,
            flag: true,
            fullWidth:document.documentElement.clientWidth //计算屏幕的宽度
        };
    },
    methods: {
       
        goback(){
            this.showchoosing = true
            this.showanalysis = false
        },
        //  年级查询
        grades() {
            this.$get('/basic/achieve/grades').then(res => {
                if (res.status == 1) {

                    this.cities = res.data

                }
            })
        },
        // 班级查询
        allClass(grade) {
            let url = `/basic/achieve/classes/${grade}`
            this.$get(url).then(res => {
                if (res.status == 1) {
                    
                    this.cities1 = res.data
                }
            })
        },
        //学生查询
        allStudent(classId) {
            let url = `/basic/achieve/students/${classId}`
            this.$get(url).then(res => {
                if (res.status == 1) {
                    this.cities2 = res.data

                    // this.cities2.unshift({id:null,name:'全部'})
                }
            })
        },
        //  计算结果
        calculate(g,c,s) {
            if (this.value == '' && g == '') {
                this.$notify({
                    title: '提示',
                    message: '请选择年级',
                    type: 'warning',
                    duration: '1500'
                });
                return;
            }
            this.showchoosing = false
            this.showanalysis = true
            this.flag = true
            let data = {
                grade: this.value || g,
                classId: this.value1 ||  c,
                studentId: this.value2 || s,
                specialtyId:localStorage.getItem('specialtyId')
            }
            this.$post('/basic/achieve/analysis/claim', data).then(res => {
                if (res.status == 1) {
                    this.falgeshow = true
                    this.tableData = res.data.claims
                    this.chartsData = res.data
                    this.title = res.data.title
                } else {
                    this.$notify({
                        title: '提示',
                        message: res.message,
                        type: 'error',
                        duration: '1500'
                    });
                }
                this.flag = false
            }).catch(err => {
                this.$notify({
                    title: '提示',
                    message: err.message,
                    type: 'error',
                    duration: '1500'
                });
                this.flag = false
            })
        },
        //  路由跳转
        handdle(row) {
            this.$router.push({
                path: "/Calculate/GraduationAch",
                query: {
                    claimId: row.claimId,
                    grade: this.value || this.$route.query.g,
                    classId: this.value1 || this.$route.query.c,
                    studentId: this.value2 || this.$route.query.s,
                    nametitle:this.title,
                    claimcode:row.claimcode
                }
            })
        }
    },
    mounted() {
        this.grades()
        if(this.$route.query.goback == 10){
            this.showchoosing = false
            this.showanalysis = true
            this.calculate(this.$route.query.g,this.$route.query.c,this.$route.query.s)
        }else{
            this.showchoosing = true
            this.showanalysis = false
        }
    },
    watch: {
        value(val) {
            this.cities1 = []
            this.cities2 = []
            this.value1 = ''
            this.value2 = ''
            this.allClass(val)
        },
        value1(val) {
            if (val) {
                this.cities2 = []
                this.value2 = ''
                this.allStudent(val)
            }
        }
    }
};
</script>

<style lang="scss">
.CalculateDegree {
    min-width: 1021px;
    // height: 100%;
    overflow: hidden;
    margin-bottom: 110px;
    .breadcrumb-pd{
        padding: 33px 0px ;
        .el-breadcrumb__item:last-child .el-breadcrumb__inner, .el-breadcrumb__item:last-child .el-breadcrumb__inner:hover, .el-breadcrumb__item:last-child .el-breadcrumb__inner a, .el-breadcrumb__item:last-child .el-breadcrumb__inner a:hover{
            color: #03B187;
        }
        .el-breadcrumb__inner.is-link, .el-breadcrumb__inner a{
            color: #696969;
        }
    }
    .analysis_Computer{
        box-sizing: border-box;
        width: 100%;
        
        // background: url("../../assets/img/bg-dachengdu.png") no-repeat  center;
        // background-size: 100%;
        position: relative;
        background-color: #fff;
        img:nth-child(1){
            width: 100%;
        }
        img:nth-child(3){
            display: block;
            margin: 0 auto;
            position: absolute; 
            left: 50%; 
            top: 50%;   
            transform: translate(-50%, -50%);
        }
        p{
            font-size:0.9vw;
            font-family:PingFang SC;
            font-weight:500;
            color:rgba(255,255,255,1);
            text-align: center;
            position: absolute; 
            left: 50%; 
            top: 35%;   
            transform: translate(-50%, -50%);
        }
    }
    .analysis{
        box-sizing: border-box;
        width: 100%;
        height: 196px;
        background: url("../../assets/img/bg-chaxun.png") no-repeat;
        background-size: 100%;
        margin-bottom: 20px;
        .analysis_top{
            padding: 28px 0 1vw 1vw ;
            padding-right: 1vw;
            display: flex;
            .analysis_left{
                flex:0.1;
                width:120px;
                height:28px;
                border:1px solid rgba(176,249,224,1);
                border-radius:14px;
                cursor: pointer;
                text-align: center;
                img{
                    width: 12%;
                }
                span{
                    font-size:14px;
                    font-family:PingFang SC;
                    font-weight:500;
                    color:rgba(176,249,224,1);
                    line-height:30px;
                }
            }
            p{  
                flex:1;
                text-align: center;
                font-size:24px;
                font-family:PingFang SC;
                font-weight:bold;
                color:rgba(255,255,255,1);
                // margin-left: 10vw;
            }
            .analysis_right{
                flex:0.1;
                width:120px;
                height:28px;
                border:1px solid rgba(176,249,224,1);
                border-radius:14px;
                padding-left: 5px;
                padding-right: 5px;
                text-align: center;
                img{
                    width: 12%;
                }
                span{
                    font-size:14px;
                    font-family:PingFang SC;
                    font-weight:500;
                    color:rgba(176,249,224,1);
                    line-height:30px;
                }
            }
        }
        .analysis_botm{
            margin-top: 22px;
            // padding-left:5vw;
            // padding-right: 5vw;
            img{
                margin: 0 auto;
                display: flex;
                // width: 100%;
            }
        }
    }
    .dv {
        margin-bottom: 60px;
        overflow: hidden;
        position: relative;
        span{
            display: block;
            text-align: center;
            margin-bottom: 19px;
            font-size:24px;
            font-family:PingFang SC;
            font-weight:bold;
            color:rgba(50,50,50,1);
        }
        .el-select{
            width: 100%;
            height: 62px;
        }
        .el-input__inner{
            height: 62px;
            line-height: 62px;
            border:1px solid rgba(221,221,221,1);
            border-radius:5px;
            font-size:18px;
            font-family:PingFang SC;
            font-weight:400;
            color:rgba(150,150,150,1);
        }
        .el-input__icon{
            line-height: 62px;
        }
    }
    .classManagement-btn {
        margin: 0 auto;
        width:400px;
        cursor: pointer;
        background:linear-gradient(90deg,rgba(3,177,135,1) 0%,rgba(142,233,202,1) 100%);
        border-radius:35px;
        padding: 19.5px 0;
        box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
        margin-bottom: 40px;
        span{
            font-size:24px;
            font-family:PingFang SC;
            font-weight:bold;
            color:rgba(255,255,255,1);
            display: block;
            text-align: center;
        }
    }
    .classManagement-btn:hover{
        box-shadow: none;
    }
    .bgc-fff {
        display: flex;
        .DegreeAchievement-dv7 {
            // text-align: left;
            margin-bottom: 40px;
            padding: 25px 35px;

            span:nth-child(1) {
                float: left;
                font-weight: 700;
                font-size: 16px;
            }

            span:nth-child(2) {
                float: right;
                border: 1px solid #DCDFE6;
                font-size: 14px;
                padding: 5px 8px;
            }
        }
        .echarts{
            flex: 1;
        }
        .chartsData_left{
            width: 45vw;
            padding-bottom: 40px; 
            float: left;
            margin-left: 20px;
            height:617px;
            overflow-y: scroll;
            padding-top: 0;
            padding-right: 15px;
            flex: 1.5;
            ul{
                li:hover{
                    border:1px solid rgba(3,177,135,1);
                    box-shadow:0px 2px 10px 0px rgba(3,177,135,0.2);
                }
                li{
                    list-style: none;
                    background-color: #fff;
                    margin-bottom: 20px;
                    padding: 30px 25px 35px 25px;
                    overflow: hidden;
                    border-radius:10px;
                    box-sizing: border-box;
                    border:1px solid #fff;
                    cursor: pointer;
                    .chartsData_p{
                        width:44px;
                        height:44px;
                        border:1px solid rgba(204,204,204,1);
                        border-radius:50%;
                        position: relative;
                        text-align: center;
                        // display: inline-block;
                        float: left;
                        margin-right: 29px;
                        .chartsData_sp{
                            width:14px;
                            height:14px;
                            background:rgba(3,177,135,1);
                            border-radius:50%;
                            position: absolute;
                            top: 0;
                            left: 0;
                        }
                        i{
                            margin-top: 11px;
                            display: block;
                            font-style:normal;
                            font-size:16px;
                            font-family:PingFang SC;
                            font-weight:500;
                            color:rgba(105,105,105,1);
                        }
                    }
                    .chartsData_leftdv{
                        // float: left;
                        display: flow-root;
                        .chartsData_frist{
                            font-size:14px;
                            font-family:PingFang SC;
                            font-weight:bold;
                            color:rgba(50,50,50,1);
                            margin-bottom: 8px;
                        }
                        .chartsData_tow{
                            font-size:12px;
                            font-family:PingFang SC;
                            font-weight:bold;
                            color:#b5b5b5;
                            margin-bottom: 8px;
                        }
                    }
                    .chartsData_bottom{
                        margin-top: 20px;
                        display: flex;
                        span{
                            width:240px;
                            height:32px;
                            border:1px solid rgba(238,238,238,1);
                            flex:1;
                            font-size:12px;
                            display: block;
                            line-height: 32px;
                            text-align: center;
                        }
                        span:nth-child(1){
                            color:rgba(3,177,135,1);
                        }
                        span:nth-child(2){
                            color:rgba(255,163,81,1);
                        }
                        span:nth-child(3){
                            color:rgba(32,152,255,1);
                        }
                    }
                }
            }
        }
    }
}
</style>



